<template>
	<div class="toast">

		<div  >
		<div class="header_name">添加新应用 <div class="delete" v-on:click="back">×</div></div>
		<ul>
			<li>
				<div class="name">应用logo <span>*</span> </div>
				<div style='text-align: center;'>
					<Avatar :src='newdata.logo' v-if='newdata.logo'></Avatar>
					<Upload  
					:format="['jpg','jpeg','png']" 
					action="http://139.224.22.192/api/file/api/upload" 
					v-model='newdata.logo'
					:headers = 'headers'
					ref='upload'
					:data='{"imageType":"corp"}'

					>
						<Button type='ghost' icon='ios-cloud-upload-outline'>上传图片</Button>
					</Upload>
				</div>
			</li>
			<li>
				<div class="name">应用名称 <span>*</span></div>
				<div> <Input v-model="newdata.name" style="width: 200px"></Input></div>
			</li>
			
			<li>
				<div class="name">功能介绍 <span>&nbsp;&nbsp;</span></div>
				<div><Input v-model="newdata.brief" style="width: 200px"></Input></div>
			</li>
			<li>
				<div class="name">微应用首页 <span>*</span></div>
				<div><Input v-model="newdata.homepageUrl" style="width: 200px"></Input></div>
			</li>
			<li>
				<div class="name">微应用后台 <span>*</span></div>
				<div><Input v-model="newdata.ompLink" style="width: 200px"></Input></div>
			</li>
			<li>
				<div class="name">微应用管理后台 <span> </span></div>
				<div><Input v-model="newdata.pcHomepageUrl" style="width: 200px"></Input></div>
			</li>
			<!--<li>
				<div class="name">可见范围 <span> </span></div>
				<div>
					<div> <input type="radio" name="office" /> 全部员工</div>
					<div> <input type="radio" name="office" /> 部分员工</div>
					<p>应用需要 读取可见范围内人员的通讯 录基本信息</p>
				</div>
			</li>
			<li>
				<div class="name">其他 <span> </span></div>
				<div>
					<div><input type="checkbox" />公管理员可见</div>
					<p>选择后，除管理员处，所有人员不可见</p>
				</div>
			</li>-->
		</ul>
		
		</div>
		<div class="bottom_s">
			<Button class="button_s" v-on:click='submit()' :loading='loading'>提交</Button>
		</div>
	</div>
</template>

<script>
	export default{
			name:'toast',
			data(){
				return {
					pagetype:'',
					newdata:{
						name:'',
						homepageUrl:'',
						logo:'',
						brief:'',
						ompLink:'',
						active:true,
						id:'',
						pcHomepageUrl:""
					},
					loading:false,
					headers:{},
				}
			},
			methods:{
				back(){
					
					this.$router.go(-1)
				},
				submit(){
					let test =(data,message)=>{
						if(data==''){
						 	this.$Message.warning(message)	
						 	return true
						}
						return false
					}
					
					
					if(test(this.newdata.name,'请输入应用名称'))return;
					if(test(this.newdata.logo,'请上传应用图片'))return;
					if(test(this.newdata.homepageUrl,'请输入应用首页地址'))return;
					if(test(this.newdata.ompLink,'请输入应用后台地址'))return;
					this.loading=true
					if(this.newdata.id){
							this.DATAFORM.request({url:'microapp/update',data:this.newdata,method:'post'}).then(res =>{
								if(res.errmsg=='ok'){	
									this.loading=false
									this.$Message.success("修改成功")
									this.DATAFORM.dataform({addapp:true});
									this.$router.go(-1)
								}
							})
							
							
					}else{
							this.DATAFORM.request({url:'microapp/create',data:this.newdata,method:'post'}).then(res =>{
								if(res.errmsg=='ok'){	
									this.loading=false
										this.$Message.success("添加成功")
									this.DATAFORM.dataform({addapp:true});
									this.$router.go(-1)
								}
							})
						
					}
					setTimeout(()=>{this.loading=false},3000)
				
			},
			getapplist(ID){
				this.DATAFORM.request({url:'microapp/info',data:{id:ID}}).then(res =>{
				this.newdata = res.data
			})
			
	
		},
		upload(url){
				let _this = this;
			     const client = new OSS.Wrapper({
			     	region: 'oss-cn-hangzhou',
	                accessKeyId: 'LTAIiPjIMa4VhuX3',
	                accessKeySecret: 'KXrCsmSoVNdxVAAfn0LDmzX3P9oplm',
	                bucket: 'landa-mas'
			     })
			     
			     let name = url.name
	
			     	client.multipartUpload(name,url,{}).then(res =>{
			     	_this.newdata.logo = res.url;
			     	
			     	console.log(res)
			     }).catch(err =>{
			     	console.error(err)
			     })
			
			     return false
			
		}
		},
		created(){
			if(this.$route.query.id){
				this.getapplist(this.$route.query.id)
			}
			this.headers = {
				Accept:'application/json',
				'Content-Type':'multipart/form-data'
			};
			let token =this.DATAFORM.gettoken();
			this.headers = $.extend(this.headers,token)
			
		}
		}
</script>

<style scoped>

.show_card>div{
	width: 100%;
	height: 25px;
	text-align: center;
	font-size: 14px;
	color: #303030;
	line-height: 25px;
}

.show_card>div:hover{
	background: #38adff;
	color: #FFFFFF;
}

.font-38adff{
	color: #38adff !important;
}





.toast{
	width: 600px;
	height: 564px;
	position: fixed;
	margin-left: -300px;
	margin-top: -282px;
	top: 50%;
	left: 50%;
	box-shadow: 0 0 8px 2px rgba(0,0,0,0.3);
	border-radius: 10px;
	background: #FFFFFF;
	z-index:99
}

.toast .header_name{
	width: 100%;
	height:55px;
	border-bottom: #DEDEDE solid 1px;
	text-indent: 20px;
	line-height: 55px;
	margin-bottom: 0;
	padding: 0;
	position: relative;
}

.toast .header_name .delete{
	position:absolute;
	width: 28px;
	height: 28px;
	font-size: 20px;
	color: #999;
	font-weight: bolder;
	top: 5px;
	right: 10px;
	text-indent: initial;
	cursor: pointer;
}

.toast .bottom_s{
	border-top: #DEDEDE solid 1px;
	width: 100%;
	height:55px;
	text-indent: 20px;
	line-height: 55px;
	position: absolute;
	bottom: 0;
	left: 0;
}

.toast .bottom_s .button_s{
	min-width: 53px;
	height: 33px;
	position: absolute;
	top: 14px;
	right: 13px;
	background: #38ADFF;
	color: #FFFFFF;
	text-align: center;
	border: 1px solid #357ebd;
	cursor: pointer;
	text-indent: initial;
}

.toast>div>ul{
	width: 100%;
	margin-top:15px ;
}

.toast>div>ul>li{
	width: 100%;
	margin-bottom: 20px;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
}

.toast>div>ul>li>div{
	float: left;
}
.toast>div>ul>li .name{
	width: 150px;
	margin-right: 20px;
	font-size: 14px;
	color: #303030;
	text-align: right;
}

.toast>div>ul>li .name span{
	margin:0 3px;
	font-size: 12px;
	color: #ff2828;
}

.toast>div>ul>li img{
	width: 80px;
	height: 77px;
	margin-bottom: -15px;
}

.toast>div>ul>li div{
	font-size: 14px;
	color: #303030;
	margin: 3px;
}

.toast>div>ul>li div p{
	font-size: 12px;
	display: block;
	color: #666;
		
}
</style>


